import React from "react";
import Subject from "./Subject";
import './Day.css';
import "firebase/database";
import FirebaseService from "./FirebaseService";

export default class Day extends React.Component {

    onSubjectChange(subjectKey, subjectTitle, newText) {
        const { dayName } = this.props;
        const dbPath = '/week1/' + dayName.toLowerCase() + '/' + subjectKey;

        FirebaseService.databaseRef.child(dbPath).once('value').then((snapshot) => {
            var today = snapshot.val();
            today = {...today,
                title: subjectTitle,
                text: newText,
            };
            console.log('### today = ', today);
            FirebaseService.databaseRef.child(dbPath).set(today);
        });
    }

    render() {
        const { day, dayName } = this.props;
        if (!day) return null;
        const subjectsKeys = Object.keys(day); // TODO: sort
        return (
            <div className="Day">
                <div className="DayName">
                    {dayName}
                </div>
                <div>
                    {subjectsKeys && subjectsKeys.map((subjectKey) =>
                        <Subject subject={day[subjectKey]}
                                 dbPath={'/week1/' + dayName.toLowerCase() + '/' + subjectKey}
                                 key={day[subjectKey].title+subjectKey}
                                 onSubmit={(newText)=>{
                                     this.onSubjectChange(subjectKey, day[subjectKey].title, newText);
                                 }}
                        />
                    )}
                </div>
            </div>
        );
    }
}
