import React from 'react';
import logo from './loga.png';
import './CurrentWeek.css';
import "firebase/database";
import Week from "./Week";
import info from './info.png';
import FirebaseService from './FirebaseService'
import {NavLink} from "react-router-dom";

export default class CurrentWeek extends React.Component {

    state = {
        week1: []
    };

    componentDidMount() {
        FirebaseService.week1Ref.on("value", snapshot => {
            const week1 = snapshot.val();
            // console.log('### week1 = ', week1);
            this.setState({week1})
        });
    }

    render() {
        const { week1 } = this.state;

        return (
                <div className="App">

                    <header className="App-header">
                        <div className="LogoContainer">
                            <img src={logo} className="App-logo" alt="logo" />
                            <NavLink className="" activeClassName="" to="/info">
                                <img src={info} className="Info-icon" alt="info" />
                            </NavLink>
                        </div>
                        <Week week={week1}/>
                    </header>

                </div>
        );
    }
}
