import React from "react";
import './Subject.css';
import skrepaka from './skrepaka.png';
import {NavLink, useHistory} from "react-router-dom";
import info from "./info.png";

export default class Subject extends React.Component {

    constructor(props) {
        super(props);
        const { subject } = props;
        const { text } = subject;

        this.state = { text, editing: false };
    }

    componentDidMount() {
        const {editing} = this.state;

        if (editing) {
            this.nameInput.focus();
        }
    }

    handleChange(event) {
        // console.log('### event.target.value = ', event.target.value);
        this.setState({text: event.target.value});
    }

    handleSubmit(event) {
        const { onSubmit } = this.props;
        const { text } = this.state;
        // console.log('### handleSubmit = ', text);

        event.preventDefault();

        onSubmit && onSubmit(text);
        this.setState({editing: false});
    }

    onFilesIconClicked() {
//        this.props.history.push('/files');
        // useHistory().push("/files");
    };

    render() {
        const { subject, dbPath } = this.props;

        // console.log('### subject = ', subject);

        const { title } = subject;
        const { text, editing } = this.state;
        // console.log('### title = ', title);

        const isEmpty = text && text.replace(/\s+/, '').length === 0;
        // console.log(`### text = [${text}]`);
        // console.log(`### isEmpty = [${isEmpty.length}]`);

        return (
            <div className="Subject">
                <div className="Title">{title}</div>
                <div className="Seperator" />
                <div className="SubjectTextButton"  onClick={()=>{
                    this.setState({editing: true})
                }}>
                    {editing && <div className="EditContainer">
                        <form onSubmit={this.handleSubmit.bind(this)} className="EditForm">
                            <input type="text" autoFocus value={text} className="TextInput"
                                   onChange={this.handleChange.bind(this)}
                                   onBlur={this.handleSubmit.bind(this)}
                            />
                            <button type="submit" className="SubmitButton">Save</button>
                        </form>
                    </div>}

                    {!editing && <div className="SubjectText"> {text} </div>}
                    {!editing && !isEmpty &&
                        <NavLink className="" activeClassName="" to={
                        { pathname: '/files', state: { dbPath } }
                    }>
                        <img src={skrepaka} className="Skrepaka" alt="skrepaka" />
                        </NavLink>}
            </div>
            </div>
        );
    }
}
