import React from "react";
import './FilesView.css';
import {NavLink} from "react-router-dom";
import back from "./back.png";
import FirebaseService from "./FirebaseService";
import { FilePicker, ImagePicker } from 'react-file-picker'

export default class FilesView extends React.Component {

    state = {
        file: null,
        uploading: false,
        title: null,
        text: null,
        files: [],
    };

    // constructor(props) {
    //     super(props);
        // this.inputOpenFileRef = React.createRef()
    // }

    componentDidMount() {
        this.loadFiles()
    }

    loadFiles() {
        const { dbPath } = this.props.location.state;

        // FirebaseService.loadFilesRefs((filesRefs)=> this.setState({ filesRefs }) )
        FirebaseService.databaseRef.child(dbPath).on("value", snapshot => {
            const val = snapshot.val();
            // console.log('### week1 = ', val);
            this.setState({title: val.title, text: val.text, files: val.files})

            // console.log('### files = ', val.files);
            // const filesKeys = Object.keys(val.files);
            // FirebaseService.loadFileRef('', (fileRef) => {
            //    console.log('### fileRef = ', fileRef);
            // });
            // console.log('### filesKeys = ', filesKeys);
            // const ref = FirebaseService.filesRef.child(filesKeys[0]);
            // ref.getDownloadURL().then(url => console.log('### value = ',url));
            // console.log('### ref = ', ref);
        });

    }

    onFormSubmit(e){
        e.preventDefault();
        this.fileUpload(this.state.file);
    }
    onChange(e) {
        this.setState({file:e.target.files[0]})
    }

    fileUpload(file) {
        const { dbPath } = this.props.location.state;

        this.setState({ uploading: true });
        // console.log('### fileUpload file ', file);
        const UUID = uuidv4();
        const newFileRef = FirebaseService.filesRef.child(UUID);
        newFileRef.put(file).then(s => {

            // console.log('### dbPath = ', dbPath);
            FirebaseService.databaseRef.child(dbPath).once('value').then((snapshot) => {
                var today = snapshot.val();
                today.files = {...today.files, [UUID]: { name: file.name }};
                // console.log('### today = ', today);
                FirebaseService.databaseRef.child(dbPath).set(today);
            });

            this.loadFiles();
            this.setState({ uploading: false });
            // console.log('Uploaded a blob or file!');
        });
    }

    // showOpenFileDlg = () => {
    //     this.inputOpenFileRef.current.click()
    // };

    render() {
        const { files, uploading, title, text } = this.state;
        const filesKeys = files && Object.keys(files) || [];

        console.log('### filesKeys = ', filesKeys);

        return (
            <div className="FilesView">
                <div className="BackIconView">
                    <NavLink className="BackIconView" activeClassName="" to="/">
                        <img src={back} className="BackIcon" alt="back" />
                    </NavLink>
                </div>

                <p/><p/>
                <div> {title} - {text}</div>
                <p/><p/>

                <div className="FilesViewTitle"> Files </div>
                {filesKeys.length > 0 && filesKeys.map((fileUUID)=>{
                    // console.log('### fileRef = ', fileRef);
                    return <FileLink key={fileUUID}
                                     fileName={files[fileUUID].name}
                                     fileUUID={fileUUID} />
                })
                }

                <p/><p/>
                {uploading && <div>Uploading...</div>}
                {!uploading &&
                <form onSubmit={this.onFormSubmit.bind(this)} className="FileUploadForm">
                    <div>File Upload</div>
                    <input type="file" onChange={this.onChange.bind(this)} />
                    {/*<input ref={this.inputOpenFileRef} type="file" style={{display:"none"}}/>*/}
                    {/*<div onClick={this.showOpenFileDlg}>Add a file</div>*/}

                    <div className="SubmitButtonView">
                        <button type="submit"> Upload </button>
                    </div>
                </form>}

            </div>
        )
    }
}

const uuidv4 = () => {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        const r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });
};

class FileLink extends React.Component {
    state = { url: null };

    componentDidMount() {
        const { fileUUID } = this.props;
        const fileRef = FirebaseService.filesRef.child(fileUUID);
        fileRef.getDownloadURL().then(url => this.setState({ url }) );
    }

    render() {
        const { url } = this.state;
        const { fileName } = this.props;
        // console.log('### fileName = ', fileName);
        return (
            <div>
                {url && <a href={url}>{fileName}</a>}
            </div>
        );
    }
}
